/* Container */
.pre-registration-container {
  max-width: 400px;
  margin: 2rem auto;
  padding: 1.5rem;
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

/* Form layout */
.pre-reg-form {
  display: flex;
  flex-direction: column;
}

/* Email input */
.email-input {
  padding: 0.75rem;
  margin: 0.5rem 0 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.2s;
}
.email-input:focus {
  border-color: #007bff;
  outline: none;
}

/* Wallet info text */
.wallet-info {
  margin: 0.5rem 0;
  font-size: 0.9rem;
  color: #333;
}

/* Buttons */
.button-custom {
  padding: 0.75rem;
  margin: 0.5rem 0;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.2s;
}
.button-custom:hover:not(:disabled) {
  background: #0056b3;
}
.button-custom:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.h2xd{
  color: #000000;
}

/* Disconnect button */
.disconnect {
  background: #dc3545;
}
.disconnect:hover:not(:disabled) {
  background: #a71d2a;
}

/* Error message */
.error {
  color: #dc3545;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}